import { Box, InputLabel } from '@mui/material'
import { useTheme } from '@mui/material/styles'
import { GoogleMap, useJsApiLoader } from '@react-google-maps/api'
import { GeoPoint } from 'firebase/firestore'
import { useState } from 'react'
import { MAP_KEY } from '../constants/key'

interface IMapBox {
  label?: string
  onChange?: (v: GeoPoint) => void
}

const containerStyle = {
  width: '100%',
  height: '400px'
}

export default function MapBox({ label, onChange }: IMapBox) {
  const theme = useTheme()
  const [pos, setPosCenter] = useState<{ lat: number; lng: number }>({
    lat: 13.8714115,
    lng: 100.5675974
  })
  const { isLoaded } = useJsApiLoader({
    id: 'google-map-script',
    googleMapsApiKey: MAP_KEY
  })
  const [map, setMap] = useState<google.maps.Map | null>(null)

  return (
    <Box pt={2} pb={4}>
      <InputLabel style={{ color: theme.palette.primary.dark }}>
        {`${label && label} : `}
        <span style={{ color: 'red' }}>{pos ? '' : `เลือกพิกัดที่อยู่`}</span>
      </InputLabel>
      <InputLabel style={{ color: theme.palette.primary.dark }}>
        {`ละติจูด : ${pos?.lat ?? ''} , ลองติจูด : ${pos?.lng ?? ''}`}
      </InputLabel>
      {isLoaded ? (
        <div style={{ position: 'relative' }}>
          <div
            style={{
              position: 'absolute',
              left: '50%',
              top: '50%',
              transform: 'translate(-50%,-50%)',
              zIndex: 999
            }}
          >
            <img
              src="https://developers.google.com/static/maps/documentation/javascript/images/default-marker.png"
              alt=""
            />
          </div>
          <GoogleMap
            mapContainerStyle={containerStyle}
            center={pos}
            zoom={10}
            onLoad={(map) => {
              // const bounds = new window.google.maps.LatLngBounds(center)
              // map.fitBounds(bounds)
              setMap(map)
            }}
            onUnmount={() => {
              setMap(null)
            }}
            options={{ fullscreenControl: false }}
            onDragEnd={() => {
              const c = map?.getCenter()
              if (c) {
                setPosCenter({ lat: c.lat(), lng: c.lng() })
                if (pos && onChange) {
                  onChange(new GeoPoint(c.lat(), c.lng()))
                }
              }
            }}
          ></GoogleMap>
        </div>
      ) : (
        <></>
      )}
    </Box>
  )
}
