import { yupResolver } from '@hookform/resolvers/yup'
import {
  Box,
  Container,
  FormGroup,
  RadioGroup,
  Typography
} from '@mui/material'
import { DatePicker } from '@mui/x-date-pickers/DatePicker'
import axios from 'axios'
import { GeoPoint } from 'firebase/firestore'
import { Fragment, useEffect, useState } from 'react'
import { Controller, ErrorOption, useForm } from 'react-hook-form'
import { useNavigate } from 'react-router-dom'
import * as yup from 'yup'
import AutoComplete from '../components/AutoComplete'
import CheckBox from '../components/Checkbox'
import Input from '../components/Input'
import MapBox from '../components/MapBox'
import Radio from '../components/Radio'
import { StepperRegister } from '../components/SteperRegister'
import Upload from '../components/Upload'
import { PROVINCE_DB } from '../constants/api'
import { MAX_DATE } from '../constants/key'
import { useSweet } from '../hooks/sweet'
import { BixUser } from '../models/bixUser'
import { authService } from '../services/authService'
import { useAppSelector } from '../states/hooks'
import { IProvince } from '../type'

const schema = yup.object({
  personType: yup.string(),
  gender: yup.string(),
  profile: yup
    .object({
      personCardID: yup
        .string()
        .matches(new RegExp('^[0-9]*$'), 'ใส่ตัวเลขเท่านั้น')
        .max(13, 'ไม่เกิน 13 หลัก')
        .min(13, 'ไม่น้อยกว่า 13 หลัก')
        .required('ใส่เลขประจำตัวประชาชน'),
      name: yup.string().required('ใส่ชื่อ'),
      lastName: yup.string().required('ใส่นามสกุล'),
      birthDay: yup.string().required('ใส่วันเดือนปีเกิด'),
      address: yup.string().required('ใส่สถานที่ติดต่อ'),
      province: yup.string().nullable().required('เลือกจังหวัด'),
      tumbol: yup.string().nullable().required('เลือกตำบล/แขวง'),
      ampher: yup.string().nullable().required('เลือกอำเภอ/เขต'),
      postCode: yup.string().nullable().required('เลือกรหัสไปรษณีย์'),
      secret: yup.string().when('personType', {
        is: 'company',
        then: yup
          .string()
          .matches(new RegExp('^[0-9]*$'), 'ใส่ตัวเลขเท่านั้น')
          .max(6, 'ตัวเลข 6 หลัก')
          .min(6, 'ตัวเลข 6 หลัก')
      }),
      genderDetail: yup.string().when('gender', {
        is: 'other',
        then: yup.string().required('ใส่เพศ')
      })
    })
    .required(),
  company: yup.object().when('personType', {
    is: 'company',
    then: yup.object({
      comID: yup
        .string()
        .matches(new RegExp('^[0-9]*$'), 'ใส่ตัวเลขเท่านั้น')
        .required('ใส่เลขประจำตัวผู้เสียภาษีอากร'),
      name: yup.string().required('ใส่ชื่อบริษัท'),
      address: yup.string().required('ใส่สถานที่ติดต่อ'),
      province: yup.string().nullable().required('เลือกจังหวัด'),
      tumbol: yup.string().nullable().required('เลือกตำบล/แขวง'),
      // location: yup.string().required('เลือกพิกัด'),
      ampher: yup.string().nullable().required('เลือกอำเภอ/เขต'),
      postCode: yup.string().nullable().required('เลือกรหัสไปรษณีย์'),
      web: yup.string().required('ใส่ชื่อเว็บไซต์')
    })
  }),
  tel: yup
    .string()
    .matches(new RegExp('^[0-9]*$'), 'ใส่ตัวเลขเท่านั้น')
    .required('ใส่เบอร์โทรศัพท์'),
  email: yup.string().email('รูปแบบ Email ไม่ถูกต้อง').required('ใส่ Email')
})

const RegisterPage = () => {
  const { fbUser, user } = useAppSelector((state) => state.auth)
  const [step, setStep] = useState(0)

  const email = localStorage.getItem('email')

  const defaultValues: BixUser = {
    type: 'employee',
    personType: 'person',
    jobType: {
      design: false,
      consult: false,
      construct: false
    },
    profile: {
      personCardID: '',
      name: '',
      lastName: '',
      birthDay: '01/01/2004',
      gender: 'male',
      genderDetail: '',
      address: '',
      province: '',
      tumbol: '',
      ampher: '',
      postCode: '',
      personDocUrl: '',
      personWithDocUrl: ''
    },
    tel: '',
    email: email ?? ''
    // company: {
    //   comID: '1234',
    //   name: 'ddd',
    //   address: '20/5',
    //   province: 'นนทบุรี',
    //   tumbol: 'บางกรวย',
    //   ampher: 'อำเภอบางกรวย',
    //   postCode: '11130',
    //   location: new GeoPoint(0, 0),
    //   web: 'www',
    //   tel: '1234',
    //   email: 'kkk@mail.com',
    //   companyCerUrl: '',
    //   memorandumUrl: ''
    // }
  }

  const {
    handleSubmit,
    control,
    setValue,
    watch,
    getValues,
    formState: { isValid },
    reset,
    trigger,
    setError
  } = useForm<BixUser>({
    resolver: yupResolver(schema),
    mode: 'all',
    defaultValues
  })

  const zipCode = watch('profile.postCode')
  const zipCodeCom = watch('company.postCode')
  const type = watch('type')
  const personType = watch('personType')
  const gender = watch('profile.gender')

  const [provinceList, setProvinceList] = useState<IProvince[]>([])
  const { alertError } = useSweet()
  const navigate = useNavigate()

  const getProvinceData = async () => {
    const res = await axios(PROVINCE_DB)
    setProvinceList(res.data)
  }
  useEffect(() => {
    if (fbUser && user) {
      navigate('/')
    }
    getProvinceData()
  }, [fbUser, navigate, user])

  const onSubmit = async (data: BixUser) => {
    if (!data.profile.personDocUrl || !data.profile.personWithDocUrl) {
      alertError({ title: 'Fail', text: 'อัพโหลดบัตรและรูปถ่ายคู่บัตรประชาชน' })
      return
    }
    try {
      if (fbUser) {
        await authService.addUser(
          !data.company
            ? data
            : {
                ...data,
                company: {
                  ...data.company,
                  location:
                    data.company?.location ??
                    new GeoPoint(13.8714115, 100.5675974)
                }
              }
        )
      }
    } catch (err: any) {
      alertError({ title: 'Fail', text: err })
    }
  }

  const checkValid = (): boolean => {
    if (step === 1) {
      return isValid
    }
    return true
  }

  return (
    <Container>
      <Box pb={2}>
        <Typography variant="h5">ลงทะเบียนผู้ใช้</Typography>
      </Box>
      <form onSubmit={handleSubmit(onSubmit)}>
        <StepperRegister
          isValid={checkValid()}
          step1={
            <Fragment>
              <Controller
                name="type"
                control={control}
                render={({ field: { value, ref, ...rest } }) => (
                  <RadioGroup row value={value}>
                    <Radio
                      {...rest}
                      label="ผู้รับจ้าง"
                      inputRef={ref}
                      value="employee"
                    />
                    <Radio
                      {...rest}
                      label="ผู้จ้าง"
                      inputRef={ref}
                      value="employer"
                    />
                  </RadioGroup>
                )}
              />
              <Controller
                name="personType"
                control={control}
                render={({ field: { value, ref, ...rest } }) => (
                  <RadioGroup row value={value}>
                    <Radio
                      {...rest}
                      label="บุคคลธรรมดา"
                      inputRef={ref}
                      value="person"
                    />
                    <Radio
                      {...rest}
                      label="นิติบุคคล"
                      inputRef={ref}
                      value="company"
                    />
                  </RadioGroup>
                )}
              />
            </Fragment>
          }
          step2={
            <Box maxWidth="600px" margin="auto">
              {type === 'employee' && (
                <FormGroup row>
                  <Controller
                    name="jobType.design"
                    control={control}
                    render={({ field: { ref, value, ...rest } }) => (
                      <CheckBox
                        {...rest}
                        label="ผู้รับจ้างออกแบบ"
                        inputRef={ref}
                        checked={value}
                      />
                    )}
                  />
                  <Controller
                    name="jobType.consult"
                    control={control}
                    render={({ field: { ref, value, ...rest } }) => (
                      <CheckBox
                        {...rest}
                        label="ผู้รับจ้างควบคุมและตรวจสอบงาน/ที่ปรึกษา"
                        inputRef={ref}
                        checked={value}
                      />
                    )}
                  />
                  <Controller
                    name="jobType.construct"
                    control={control}
                    render={({ field: { ref, value, ...rest } }) => (
                      <CheckBox
                        {...rest}
                        label="ผู้รับจ้างก่อสร้าง"
                        inputRef={ref}
                        checked={value}
                      />
                    )}
                  />
                </FormGroup>
              )}

              {personType == 'company' && (
                <Fragment>
                  <Box pt={4}>
                    <Typography variant="h5">ข้อมูลนิติบุคคล</Typography>
                  </Box>
                  <Controller
                    name="company.comID"
                    control={control}
                    render={({
                      field: { ref, value, ...rest },
                      fieldState: { error }
                    }) => {
                      return (
                        <Input
                          {...rest}
                          value={value ?? ''}
                          label="เลขทะเบียนนิติบุคคล"
                          inputRef={ref}
                          error={!!error?.type}
                          helperText={error?.message}
                        />
                      )
                    }}
                  />
                  <Controller
                    name="company.name"
                    control={control}
                    render={({
                      field: { ref, value, ...rest },
                      fieldState: { error }
                    }) => {
                      return (
                        <Input
                          {...rest}
                          value={value ?? ''}
                          label="นิติบุคคล"
                          inputRef={ref}
                          error={!!error?.type}
                          helperText={error?.message}
                        />
                      )
                    }}
                  />
                  <Controller
                    name="company.address"
                    control={control}
                    render={({
                      field: { ref, ...rest },
                      fieldState: { error }
                    }) => (
                      <Input
                        {...rest}
                        label="สถานที่ติดต่อ"
                        minRows={3}
                        multiline
                        inputRef={ref}
                        error={!!error?.type}
                        helperText={error?.message}
                      />
                    )}
                  />
                  <Controller
                    name="company.postCode"
                    control={control}
                    render={({
                      field: { ref, ...rest },
                      fieldState: { error }
                    }) => (
                      <AutoComplete
                        label="รหัสไปรษณีย์"
                        options={[
                          ...new Set(provinceList.map((e) => e.PostCodeMain))
                        ]}
                        {...rest}
                        onChangeAT={(e, v) => {
                          setValue('company.postCode', v)
                          setValue('company.province', '')
                          setValue('company.ampher', '')
                          setValue('company.tumbol', '')
                        }}
                        inputRef={ref}
                        error={!!error?.type}
                        helperText={error?.message}
                      />
                    )}
                  />
                  <Controller
                    name="company.province"
                    control={control}
                    render={({
                      field: { ref, value, ...rest },
                      fieldState: { error }
                    }) => (
                      <AutoComplete
                        label="จังหวัด"
                        options={[
                          ...new Set(
                            provinceList
                              .filter((e) => e.PostCodeMain === zipCodeCom)
                              .map((e) => e.ProvinceThai)
                          )
                        ]}
                        {...rest}
                        onChangeAT={(e, v) => {
                          setValue('company.province', v)
                        }}
                        value={value}
                        inputRef={ref}
                        error={!!error?.type}
                        helperText={error?.message}
                      />
                    )}
                  />
                  <Controller
                    name="company.ampher"
                    control={control}
                    render={({
                      field: { ref, ...rest },
                      fieldState: { error }
                    }) => (
                      <AutoComplete
                        label="อำเภอ/เขต"
                        options={[
                          ...new Set(
                            provinceList
                              .filter((e) => e.PostCodeMain === zipCodeCom)
                              .map((e) => e.DistrictThai)
                          )
                        ]}
                        {...rest}
                        onChangeAT={(e, v) => {
                          setValue('company.ampher', v)
                        }}
                        inputRef={ref}
                        error={!!error?.type}
                        helperText={error?.message}
                      />
                    )}
                  />
                  <Controller
                    name="company.tumbol"
                    control={control}
                    render={({
                      field: { ref, ...rest },
                      fieldState: { error }
                    }) => (
                      <AutoComplete
                        label="ตำบล/แขวง"
                        options={[
                          ...new Set(
                            provinceList
                              .filter((e) => e.PostCodeMain === zipCodeCom)
                              .map((e) => e.TambonThai)
                          )
                        ]}
                        {...rest}
                        onChangeAT={(e, v) => {
                          setValue('company.tumbol', v)
                        }}
                        inputRef={ref}
                        error={!!error?.type}
                        helperText={error?.message}
                      />
                    )}
                  />
                  <MapBox
                    label="เลือกพิกัดที่อยู่"
                    onChange={(lo) => {
                      setValue('company.location', lo)
                    }}
                  />
                  <Controller
                    name="company.web"
                    control={control}
                    render={({
                      field: { ref, value, ...rest },
                      fieldState: { error }
                    }) => {
                      return (
                        <Input
                          {...rest}
                          value={value ?? ''}
                          label="เว็บไซต์"
                          inputRef={ref}
                          error={!!error?.type}
                          helperText={error?.message}
                        />
                      )
                    }}
                  />
                </Fragment>
              )}
              <Box pt={4}>
                <Typography variant="h5">ข้อมูลผู้ใช้</Typography>
              </Box>
              <Controller
                name="profile.personCardID"
                control={control}
                render={({
                  field: { ref, ...rest },
                  fieldState: { error }
                }) => (
                  <Input
                    {...rest}
                    label="เลขประจำตัวประชาชน"
                    inputRef={ref}
                    error={!!error?.type}
                    helperText={error?.message}
                  />
                )}
              />
              <Controller
                name="profile.name"
                control={control}
                render={({
                  field: { ref, ...rest },
                  fieldState: { error }
                }) => (
                  <Input
                    {...rest}
                    label="ชื่อ"
                    inputRef={ref}
                    error={!!error?.type}
                    helperText={error?.message}
                  />
                )}
              />
              <Controller
                name="profile.lastName"
                control={control}
                render={({
                  field: { ref, ...rest },
                  fieldState: { error }
                }) => (
                  <Input
                    {...rest}
                    label="นามสกุล"
                    inputRef={ref}
                    error={!!error?.type}
                    helperText={error?.message}
                  />
                )}
              />
              <Controller
                name="tel"
                control={control}
                render={({
                  field: { ref, ...rest },
                  fieldState: { error }
                }) => (
                  <Input
                    {...rest}
                    label="เบอร์โทรศัพท์"
                    inputRef={ref}
                    error={!!error?.type}
                    helperText={error?.message}
                  />
                )}
              />
              <Controller
                name="email"
                control={control}
                render={({
                  field: { ref, ...rest },
                  fieldState: { error }
                }) => (
                  <Input
                    {...rest}
                    disabled={true}
                    label="Email"
                    inputRef={ref}
                    error={!!error?.type}
                    helperText={error?.message}
                  />
                )}
              />
              {personType === 'company' && (
                <Controller
                  name="profile.secret"
                  control={control}
                  render={({
                    field: { ref, value, ...rest },
                    fieldState: { error }
                  }) => (
                    <Input
                      {...rest}
                      value={value ?? ''}
                      label="รหัสลับ 6 ตัว"
                      type="password"
                      inputRef={ref}
                      error={!!error?.type}
                      helperText={error?.message}
                    />
                  )}
                />
              )}
              <Controller
                name="profile.gender"
                control={control}
                render={({ field: { value, ref, ...rest } }) => (
                  <RadioGroup
                    row
                    value={value}
                    onChange={() => {
                      trigger()
                    }}
                  >
                    <Radio {...rest} label="ชาย" inputRef={ref} value="male" />
                    <Radio
                      {...rest}
                      label="หญิง"
                      inputRef={ref}
                      value="female"
                    />
                    <Radio
                      {...rest}
                      label="อื่นๆ"
                      inputRef={ref}
                      value="other"
                    />
                    <Controller
                      name="profile.genderDetail"
                      control={control}
                      render={({
                        field: { ref, value, ...rest },
                        fieldState: { error }
                      }) => (
                        <Input
                          {...rest}
                          value={value ?? ''}
                          label="เพศอื่นๆ"
                          disabled={gender !== 'other'}
                          inputRef={ref}
                          error={!!error?.type}
                          helperText={error?.message}
                        />
                      )}
                    />
                  </RadioGroup>
                )}
              />
              <Controller
                name="profile.birthDay"
                control={control}
                render={({
                  field: { ref, value, ...rest },
                  fieldState: { error }
                }) => (
                  <DatePicker
                    label="วัน/เดือน/ปี เกิด"
                    {...rest}
                    inputRef={ref}
                    value={value}
                    views={['year', 'month', 'day']}
                    disableFuture={true}
                    maxDate={MAX_DATE}
                    onChange={(v) => {
                      if (v) {
                        setValue('profile.birthDay', v)
                        const er: ErrorOption = {}
                        setError('profile.birthDay', er)
                      }
                    }}
                    renderInput={(params) => (
                      <Input
                        {...params}
                        error={!!error?.type}
                        helperText={error?.message}
                      />
                    )}
                  />
                )}
              />
              <Controller
                name="profile.address"
                control={control}
                render={({
                  field: { ref, ...rest },
                  fieldState: { error }
                }) => (
                  <Input
                    {...rest}
                    label="สถานที่ติดต่อ/ที่อยู่ปัจจุบัน"
                    minRows={3}
                    multiline
                    inputRef={ref}
                    error={!!error?.type}
                    helperText={error?.message}
                  />
                )}
              />
              <Controller
                name="profile.postCode"
                control={control}
                render={({
                  field: { ref, ...rest },
                  fieldState: { error }
                }) => (
                  <AutoComplete
                    label="รหัสไปรษณีย์"
                    options={[
                      ...new Set(provinceList.map((e) => e.PostCodeMain))
                    ]}
                    {...rest}
                    onChangeAT={(e, v) => {
                      setValue('profile.postCode', v)
                      setValue('profile.province', '')
                      setValue('profile.ampher', '')
                      setValue('profile.tumbol', '')
                    }}
                    inputRef={ref}
                    error={!!error?.type}
                    helperText={error?.message}
                  />
                )}
              />
              <Controller
                name="profile.province"
                control={control}
                render={({
                  field: { ref, value, ...rest },
                  fieldState: { error }
                }) => (
                  <AutoComplete
                    label="จังหวัด"
                    options={[
                      ...new Set(
                        provinceList
                          .filter((e) => e.PostCodeMain === zipCode)
                          .map((e) => e.ProvinceThai)
                      )
                    ]}
                    {...rest}
                    onChangeAT={(e, v) => {
                      setValue('profile.province', v)
                    }}
                    value={value}
                    inputRef={ref}
                    error={!!error?.type}
                    helperText={error?.message}
                  />
                )}
              />
              <Controller
                name="profile.ampher"
                control={control}
                render={({
                  field: { ref, ...rest },
                  fieldState: { error }
                }) => (
                  <AutoComplete
                    label="อำเภอ/เขต"
                    options={[
                      ...new Set(
                        provinceList
                          .filter((e) => e.PostCodeMain === zipCode)
                          .map((e) => e.DistrictThai)
                      )
                    ]}
                    {...rest}
                    onChangeAT={(e, v) => {
                      setValue('profile.ampher', v)
                    }}
                    inputRef={ref}
                    error={!!error?.type}
                    helperText={error?.message}
                  />
                )}
              />
              <Controller
                name="profile.tumbol"
                control={control}
                render={({
                  field: { ref, ...rest },
                  fieldState: { error }
                }) => (
                  <AutoComplete
                    label="ตำบล/แขวง"
                    options={[
                      ...new Set(
                        provinceList
                          .filter((e) => e.PostCodeMain === zipCode)
                          .map((e) => e.TambonThai)
                      )
                    ]}
                    {...rest}
                    onChangeAT={(e, v) => {
                      setValue('profile.tumbol', v)
                    }}
                    inputRef={ref}
                    error={!!error?.type}
                    helperText={error?.message}
                  />
                )}
              />
            </Box>
          }
          step3={
            <Box maxWidth="600px" margin="auto">
              {personType == 'company' && (
                <>
                  <Upload
                    label="แนบสำเนานิติบุคคล"
                    objectFit="contain"
                    healperText="*ไฟล์ PDF หรือ รูปภาพ"
                    imgEx="/images/dbd.gif"
                    success={(url) => {
                      setValue('company.companyCerUrl', url)
                    }}
                  />
                  <Upload
                    healperText="*ไฟล์ PDF หรือ รูปภาพ"
                    objectFit="contain"
                    imgEx="/images/kbh.gif"
                    label="แนบสำเนาหนังสือบริคณห์สนธิ"
                    success={(url) => {
                      setValue('company.memorandumUrl', url)
                    }}
                  />
                </>
              )}
              <Upload
                label="แนบบัตรประชาชน"
                success={(url) => {
                  setValue('profile.personDocUrl', url)
                }}
              />
              <Upload
                label="รูปถ่ายคู่บัตรประชาชน"
                healperText="*ถ่ายรูปให้ชัดเจน ทั้งบัตรและใบหน้า ตามภาพตัวอย่าง"
                imgEx="/images/face_id.gif"
                success={(url) => {
                  setValue('profile.personWithDocUrl', url)
                }}
              />
            </Box>
          }
          onSubmit={() => {
            const data = getValues()
            onSubmit(data)
          }}
          onNext={(i) => {
            setStep(i)
            trigger()
          }}
          onBack={(i) => {
            setStep(i)
            if (i === 0) {
              reset(defaultValues)
            }
          }}
        />
      </form>
    </Container>
  )
}
export default RegisterPage
